// Êó∂Èó¥
export const DATE = (date , fmt) => {
	var date = new Date(date*1);
	if (/(y+)/.test(fmt)) {
		fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
	}
	let o = {
		'M+': date.getMonth() + 1,
        'd+': date.getDate(),
        'h+': date.getHours(),
        'm+': date.getMinutes(),
        's+': date.getSeconds()
	};
	for (let k in o) {
		if (new RegExp(`(${k})`).test(fmt)) {
            let str = o[k] + '';
            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : ('00' + str).substr(str.length));
        }
	}
	return fmt;
}

// ‰øùÁïôÂá†‰ΩçÂ∞èÊï∞„ÄÅ‰∏çÂõõËàç‰∫îÂÖ•  amount ‰øùÁïô‰ΩçÊï∞ 
export const SAVEDECIMALS = (number, amount) =>{
    var str = '',result ;
    if (typeof +number !== 'number') {
        return '';
    }
    str = number + '';
    if(str.lastIndexOf('.') == -1) {
        return number;
    }else {
        result = +str.substring(0,str.lastIndexOf('.') + amount + 1); 
        return result;
    }
}

// Êó∂Èó¥Êà≥ËΩ¨Âπ¥Êúà
export const TOYEARMONTHSLOT = (startYear, startMonth, endYear, endMonth) =>{
	let  result = '';
	var date = new Date();
    if(!endYear || endYear == 9999){
        endYear = date.getFullYear();
        endMonth = date.getMonth() + 1;
    }
    if (parseInt(endMonth) < parseInt(startMonth)){
        endMonth = parseInt(endMonth) + 12;
        endYear = parseInt(endYear) - 1;
    }
    result += (endYear - parseInt(startYear)) ? (endYear - parseInt(startYear))+ 'Âπ¥' : '';
    result += (endMonth - parseInt(startMonth)) ? (endMonth - parseInt(startMonth))+ '‰∏™Êúà' : '';
    return result ? result : '‰∏çË∂≥‰∏Ä‰∏™Êúà';
}

